import { Component } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { fetchImages } from '../services/api';
import { Container } from './App.styled';
import { Searchbar } from '../Components/Searchbar/Searchbar';
import ImageGallery from '../Components/ImageGallery/ImageGallery';
import { ImageGalleryItem } from '../Components/ImageGalleryItem/ImageGalleryItem';
import { Button } from '../Components/Button/Button';
import { Spinner } from '../Components/Loader/Loader';
import Modal from '../Components/Modal/Modal';
import '../App.css';

export class App extends Component {
  state = {
    imageName: null,
    images: [],
    status: 'idle',
    page: 1,
    showModal: false,

    // selectedImg: null,
    modalSrc: '',
  };

  handleFormSubmit = imageName => {
    this.setState({ imageName });
  };

  handleSelectedImg = imageUrl => {
    this.setState({ selectedImg: imageUrl });
  };
  async componentDidUpdate(prevProps, prevState) {
    const { imageName, page } = this.state;

    if (prevState.imageName !== this.state.imageName) {
      if (imageName.trim() === '') {
        return toast.error('Common... write something.');
      }

      try {
        this.setState({ status: 'pending', images: [] });
        const images = await fetchImages(imageName, page);

        if (images.length === 0) {
          toast.error('Please, write something better');
          return;
        }

        this.setState(prevState => ({
          images: [...prevState.images, ...images],
          status: 'resolved',
        }));

        // this.setState({
        //   images,
        //   status: 'resolved',
        // });

        toast('Hope you are enjoy');
      } catch (error) {
        this.setState({ status: 'rejected' });
        toast.error('Error');
      }
    }
  }
  // toogleModal = data => {
  //   this.setState({
  //     showModal: data,
  //   });
  // };
  toogleModal = () => {
    this.setState(state => ({
      showModal: !state.showModal,
    }));
  };
  writeSrcState = data => {
    this.setState({
      modalSrc: data,
    });
  };

  onLoadMore = () => {
    this.setState(prevState => ({
      page: prevState.page + 1,
    }));
  };

  render() {
    const { images, status, showModal, modalSrc } = this.state;

    return (
      <Container>
        <Searchbar onSearch={this.handleFormSubmit} />
        {status === 'pending' && <Spinner />}
        <ImageGallery>
          <ImageGalleryItem
            images={images}
            toogleModal={this.toogleModal}
            writeSrcState={this.writeSrcState}
          />
          {/* ImageGallery={images}
          onSelect={this.handleSelectedImg}
          toogleModal={this.toogleModal}
          writeSrcState={this.writeSrcState} */}
        </ImageGallery>
        {images.length > 1 && <Button onClick={this.onLoadMore} />}

        {/* {isShowImageList && <ImageGallery images={images} />} */}

        {showModal && (
          <Modal
            modalSrc={modalSrc}
            showLoader={showModal}
            toogleModal={this.toogleModal}
          />
        )}
        <ToastContainer autoClose={3000} />
      </Container>
    );
  }
}
